<template>
  <div>
    <van-nav-bar title="购物车" />

    <van-checkbox-group class="card-goods" v-model="checkedGoods">
      <van-checkbox class="card-goods__item" v-for="(item,index) in goods" :key="item.id" :name="item.id">
        <van-card :title="item.title" :desc="item.desc" :num="item.num" :price="formatPrice(item.price)"
          :thumb="item.thumb" />
      </van-checkbox>
    </van-checkbox-group>
    <van-submit-bar :price="totalPrice" button-text="提交订单" @submit="onSubmit" style="margin-bottom: 55px;">
      <van-checkbox v-model="checked" @click="allSelect">全选</van-checkbox>
    </van-submit-bar>
  </div>
</template>

<script>
  export default {
    data() {
      return {

        checked: false,
        checkedGoods: [],
        goods: [{
          id: '1',
          title: '进口香蕉',
          desc: '约250g，2根',
          price: 200,
          num: 1,
          checked: true,
          thumb: 'https://img.yzcdn.cn/public_files/2017/10/24/2f9a36046449dafb8608e99990b3c205.jpeg'
        }, {
          id: '2',
          title: '陕西蜜梨',
          desc: '约600g',
          price: 690,
          num: 1,
          checked: false,
          thumb: 'https://img.yzcdn.cn/public_files/2017/10/24/f6aabd6ac5521195e01e8e89ee9fc63f.jpeg'
        }, {
          id: '3',
          title: '美国伽力果',
          desc: '约680g/3个',
          price: 2680,
          num: 1,
          checked: false,
          thumb: 'https://img.yzcdn.cn/public_files/2017/10/24/320454216bbe9e25c7651e1fa51b31fd.jpeg'
        }]
      };
    },

    computed: {
      submitBarText() {
        const count = this.checkedGoods.length;
        return '结算' + (count ? `(${count})` : '');
      },

      totalPrice() {

        return this.goods.reduce((total, item) =>
          total + (this.checkedGoods.indexOf(item.id) !== -1 ? item.price : 0), 0);
      }
    },

    methods: {
      select(index) {

        this.goods[index].checked = !this.goods[index].checked;

      },

      allSelect() {
        if (this.checked == true) {
          for (let i = 0; i < this.goods.length; i++) {
            this.checkedGoods.push(this.goods[i].id);
          }
        } else {
          this.checkedGoods = [];
        }

      },

      formatPrice(price) {
        return (price / 100).toFixed(2);
      },

      onSubmit() {
        this.$toast('点击结算');
      }
    }
  };
</script>



